import Vue from 'vue'
import VueI18n from 'vue-i18n'
import messages from './lang'
import Modules from './Modules'
import enLocale from 'element-ui/lib/locale/lang/en'
import frLocale from 'element-ui/lib/locale/lang/fr'

Vue.use(VueI18n)

_.forEach(Modules, function (module) {
  let file = './Modules/' + module.folder + '/Resources/vueapp/lang/index.js'
  let x = require(`${file}`)

  _.forEach(x.default, function (value, key) {
    messages[key][module.name.toLowerCase()] = value
  })

});

Object.assign(messages.en, enLocale)
Object.assign(messages.fr, frLocale)

console.log(messages)

export default new VueI18n({
  locale: 'fr',
  fallbackLocale: 'en',
  messages,
})



