import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import About from './views/About.vue'
import Modules from './Modules'
import _ from 'lodash'
import App from './components/layouts/app/Index'
import Dashboard from './components/layouts/dashboard/Index'
import Default from './components/layouts/default/Index'

Vue.use(Router)

let moduleRouter = [];
moduleRouter = [
  {
    path: '/',
    name: 'default',
    component: Default,
    children: []
  },
  {
    path: '/',
    name: 'app',
    component: App,
    children: [
      {
        path: '/home',
        name: 'home',
        component: Home,
      },
      {
        path: '/about',
        name: 'about',
        component: About,
      },
    ]
  }, {
    path: '/dashboard',
    name: 'dashboard',
    component: Dashboard,
    children: []
  }
]


_.forEach(Modules, function (module) {
  let file = './Modules/' + module.folder + '/Resources/vueapp/router.js'
  let x = require(`${file}`)

  if (!module.layout)
    module.layout = 'default'

  _.forEach(moduleRouter, function (route) {
    if (route.name === module.layout) {
      route.children = _.concat(route.children, x.default)
    }
  });
});

console.log(moduleRouter)

export default new Router({
  mode: 'history',
  routes: moduleRouter
})



