import Vue from 'vue'
import Vuex from 'vuex'
import Modules from './Modules'

Vue.use(Vuex)

let moduleStore = {};

_.forEach(Modules, function (module) {
  let file = './Modules/' + module.folder + '/Resources/vueapp/store.js'
  let x = require(`${file}`)
  moduleStore[module.name] = x.default
});

export default new Vuex.Store({
  state: {},
  mutations: {},
  actions: {},
  modules: moduleStore
})



