<template>
    <header>
        <button @click="change()">{{availableLang}}</button>
        <h1>Header Default</h1>
    </header>
</template>

<script>
  export default {
    name: "app-header",
    methods: {
      change() {
        if (this.$i18n.locale === 'en') {
          this.$i18n.locale = 'fr'
        } else {
          this.$i18n.locale = 'en'
        }
      }
    },
    computed: {
      availableLang() {
        return this.$i18n.locale
      }
    }
  }
</script>

<style scoped>

</style>


